import React, { useState } from "react";
import { BrowserRouter, Link, Route, Routes } from "react-router-dom";
import { WeatherData } from "./data";
import { DisplayWeather } from "./displayWeather";

export function SearchBar() {
  const [searchInput, setSearchInput] = useState("");
  const [CurrentDegree, setDegree] = useState("");
  const [newDegree, setNewDegree] = useState("");
  const handleChange = (e) => {
    e.preventDefault();
    setSearchInput(e.target.value);
  };
  const showTemp = () => {
    setNewDegree(searchInput);
    WeatherData.map((data, key) => {
      if (data.City === searchInput) return setDegree(data.Degree);
    });
  };

  return (
    <div>
      <p>Please select the city</p>

      <select class="searchbar" onChange={handleChange} value={searchInput}>
        <option>select city</option>
        {WeatherData.map((data, key) => {
          return <option key={key}>{data.City}</option>;
        })}
      </select>
      <BrowserRouter>
        <Link to="/displayWeather">
          <button class="searchbtn" onClick={() => showTemp(searchInput)}>
            Search
          </button>
        </Link>
        <Routes>
          <Route
            path="/displayWeather"
            element={
              <DisplayWeather city={newDegree} weather={CurrentDegree} />
            }
          ></Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}
